/*configuraÃ§Ã£o da checkbox*/

.checkbox_dig input[type="checkbox"] {
  --checkbox-color-unchecked: #0e78c9;
  width: 15%;
  height: 8vh;
  /* EstilizaÃ§Ã£o da checkbox */
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  outline: none;
  border: 2px solid var(--checkbox-color-unchecked);
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}

/* Ãcone de marca de seleÃ§Ã£o */
.checkbox_dig input[type="checkbox"]:checked::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;
  background-color: #0e78c9;
  border-radius: 2px;
}

